import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
    }
  },
  computed: {
    ...mapState([
      'addRemarkStatus'
    ])
  },
  methods: {
    ...mapMutations({
      setAddRemarkStatus: 'SET_ADD_REMARK_STATUS'
    }),
    // 跳转到收藏备注
    onSkipAddRemarkFn () {
      uni.navigateTo({
        url: `/pages/addRemark/index?id=${this.id}`
      })
    },
    // 隐藏添加备注
    onHideRemarkFn () {
      this.setAddRemarkStatus(false)
    }
  },
  onUnload () {
    this.setAddRemarkStatus(false)
  },
  props: {
    id: {
      type: String,
      default: ''
    }
  }
}
